.main-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative; }

.main-nav__link-logo {
  padding-top: 22px;
  min-height: 79px; }

.main-nav__toggle {
  position: absolute;
  right: 20px;
  display: block;
  padding: 10px;
  border: none;
  font-size: 0;
  cursor: pointer; }
  .main-nav__toggle--opened {
    top: 18px;
    width: 41px;
    height: 40px;
    background: url("../img/icon-menu-close.svg") no-repeat 50% 50%; }
  .main-nav__toggle--closed {
    top: 21px;
    width: 40px;
    height: 34px;
    background: url("../img/icon-menu-open.svg") no-repeat 50% 50%; }
  .main-nav__toggle--nojs {
    display: none; }

.main-nav__list {
  width: 100%;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  list-style: none; }

.nav-list__item {
  box-shadow: 0 1px 0 0 #ececec, inset 0 1px 0 0 #ececec; }

.nav-list__svg {
  margin-right: 24px;
  fill: #231f20;
  opacity: 0.2;
  cursor: pointer;
  transform: translateY(3px); }
  .nav-list__svg--cart {
    margin-right: 21px; }

.nav-list__link {
  display: flex;
  align-items: center;
  margin: 0 auto;
  max-width: 260px;
  min-height: 79px;
  font-weight: 700;
  color: #000000; }
  .nav-list__link--catalog, .nav-list__link--knitting {
    padding-left: 46px; }
  .nav-list__link:hover, .nav-list__link:focus {
    opacity: 0.6; }
  .nav-list__link:active {
    opacity: 0.3; }

.nav-list--third,
.main-nav__delivery {
  display: none; }

@media (max-width: 767px) {
  .main-nav__toggle--closed ~ .main-nav__list .nav-list__item {
    display: none; } }

@media (min-width: 768px) {
