    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    min-width: 668px; }
  .main-nav__img-logo {
    width: 112px;
    height: 46px; }
  .main-nav__link-logo {
    flex-grow: 1;
    margin-bottom: 17px;
    margin-right: 190px;
    padding-top: 22px;
    /* 22px такой же в мобайл - после всего можно удалить */
    min-height: 88px; }
  .main-nav__toggle {
    display: none; }
  .nav-list {
    width: inherit; }
    .nav-list--first {
      order: 3;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      font-size: 15px;
      background-color: #f9f9f9; }
    .nav-list--first-bg-color {
      background-color: #f1f1f1; }
    .nav-list--second {
      order: 2;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      min-height: 88px;
      /*width: 350px;*/ }
  .nav-list__item {
    box-shadow: none; }
    .nav-list__item--search {
      border-left: 1px solid rgba(0, 0, 0, 0.1);
      border-right: 1px solid rgba(0, 0, 0, 0.1); }
  .nav-list__span-hidden {
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    white-space: nowrap;
    clip-path: inset(100%); }
  .nav-list__link {
    padding: 0;
    /*чтоб не наезжало на верхнюю строку */ }
    .nav-list__link--search {
      padding-left: 31px;
      padding-right: 31px;
      min-height: 88px; }
    .nav-list__link--cart {
      margin-left: 31px;
      min-height: 88px; }
    .nav-list__link--catalog {
      margin-right: 36px; }
    .nav-list__link--knitting {
      margin-right: 110px; }
  .nav-list__svg--search {
    /*width: 82px;*/
    margin-right: 0; }
  .nav-list__svg--cart {
    margin-right: 28px; }
  .main-nav__delivery {
    display: block;
