@mixin ul-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin centering {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

@mixin container {
  /*width: 100%;*/
  margin: 0 auto;
  padding: 0 30px;

  @media (min-width: $tablet-width) {
    padding: 0 50px;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-width;
  }
}

@mixin visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
}

@mixin bg-zigzag-fill($height-zigzag: 24px) {
  position: absolute;
  content: "";
  bottom: calc(#{-$height-zigzag} / 2);
  height: $height-zigzag;
  background-image: url("../img/bg-zigzag-fill.svg");
  background-repeat: repeat-x;
  background-size: auto $height-zigzag;
  width: 100%;
}
